.results {
  margin: 0;
  padding: 0;
  list-style: none;
  background: #EDE9E0;

  li {
    list-style: none;
    border-bottom: solid 1px #BEBAB3;
  }
}

.screen--end, .screen--game {
  display: none;
}

.screen--end{
  text-align: center;

  h2{
    font-size: 40px;
    color: $green;
    text-transform: uppercase;
    font-weight: 200;
  }
}

.results--item{
  text-align: left;
  padding:6px;
  vertical-align: middle;
  color: $lime;
  .wrong-icon{
    display: none;
  }
  &.wrong{
    color: $red;
    .wrong-icon{
      display: inline-block;
    }

    .correct-icon{
      display: none;
    }
  }
  
  &:after{
    display: block;
    content: '';
    clear: both;
  }
}

.btn--more-details{
  float: right;
  font-size: 12px;
  padding: 6px;
}

.correct-icon, .wrong-icon{
  vertical-align: middle;
  display: inline-block;
  height:25px;
  width: 25px;
  margin-right: 15px;
  padding: 11px 5px;
}
.correct-icon{
  &:before, &:after{
    display: block;
    content: '';
    height:2px;

    background: $lime;
    transform-origin: center center;
  }
  &:before{
    width: 10px;
    transform: rotate(45deg) translateY(7px);
  }

  &:after{
    width: 20px;
    transform: rotate(-45deg);
  }

}
.wrong-icon{
  &:before, &:after{
    display: block;
    content: '';
    height:2px;
    width: 20px;
    background: $red;
    transform-origin: center center;
  }
  &:before{
    transform: rotate(-45deg) translateX(-1px);
  }

  &:after{
    transform: rotate(45deg) translateX(-1px);
  }
}

.time-holder, .answers-holder{
  font-weight: bold;
  color: $lime;
}

.bottom-wrapper--header{
  font-weight: 200;
  text-transform: uppercase;
  color: $light_gray;
  margin-bottom: 40px;
  font-size: 22px;
}

.end-game-buttons{
  margin: 20px 0;

  .btn{
    font-size: 14px;
    padding:8px;
    vertical-align: middle;
    margin: 10px;

  }
}